<template>
  <div class="container content">
    <div class="row">
      <div class="col-md-12">
        <div class="div-default title">
          <div>
            <a class="go-back" @click="back">返回博客</a>
            <div class="reward" @click="pop">打赏</div>
          </div>
        </div>
      </div>
      <div class="col-md-12 passageContent">
        <div class="div-default">
          <div class="title main-top">
            <div>
              <i class="fa fa-tags"></i>
              <span class="ptitle" v-text="data.ptitle"></span>
              <div class="pview">
                阅读量：<span v-text="data.pviewnum"></span>
              </div>
            </div>
          </div>
          <hr class="line-hr" />
          <div class="box">
            <div v-show="loading">
              <skeleton type="img" line="1"></skeleton>
              <skeleton type="text" line="1"></skeleton>
            </div>
            <article class="article" v-show="!loading">
              <div>
                <div class="detail" v-html="data.pcontent"></div>
              </div>
            </article>
            <span class="ptime next" @click="next" v-if="nextBtn">下一篇</span>
            <span class="ptime" v-text="data.ptime"></span>
            <div style="clear: both"></div>
          </div>
        </div>
      </div>
      <div class="col-md-12 none-768 passage-hot">
        <div class="div-default">
          <div class="title">
            <div>热门文章</div>
          </div>
          <hr class="line-hr" />
          <div class="box passages-other">
            <div v-for="name in articles" :key="name.pid">
              <i class="fa fa-check-square"></i>&nbsp;
              <span v-text="name.ptitle" :pid="name.pid" @click="detail"></span>
              <span v-text="name.pkeywords" class="other-keywords"></span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="popup" v-if="popup == true" @click="pop">
      <img src="https://cdn.jsdelivr.net/gh/asuperdog/myrepo@master/myReward.jpg" />
    </div>
  </div>
</template>

<script>
  // import hljs from "highlight.js";
  // import "highlight.js/styles/atom-one-dark.css";
  import skeleton from "@/components/skeleton";

  export default {
    name: "passage",
    props: ["pid_old"],
    components: {
      skeleton
    },
    data() {
      return {
        data: {},
        articles: [],
        popup: false,
        loading: true,
        nextBtn: false,
        pid: this.pid_old,
      }
    },

    created() {
      this.getData()
      // console.log("store.state.pNumber", this.$store.state.pNumber)
    },

    methods: {
      //文章数据获取
      getData: function() {
        var _data1 = {
          pid: this.pid,
        }
        var r = /(src="Blog\/img\/favicon1\.png"\spsrc|psrc)/g
        // this.$axios({
        //   method: 'post',
        //   url: '/blog/getPassageDetail',
        //   data: _data1
        // }).then(res => {
        //   res[0].pcontent = res[0].pcontent.replace(r, 'src')
        //   this.res = res[0]
        //   document.title = this.res.ptitle
        //   setTimeout(() => {
        //     this.loading = false
        //   }, 800)
        // }).catch(err => {
        //   console.log(err)
        // })

        this.$axios({
          method: 'get',
          url: 'static/src/toplist.json',
        }).then(res => {
          this.articles = res.data
        }).catch(err => {
          console.log(err)
        })

        let pid_arr = this.$store.state.pid_array
        let num = pid_arr.indexOf(this.pid)
        // console.log(pid_arr.length, num)
        if (pid_arr.length - 1 > num) {
          this.pid = pid_arr[num + 1]
          this.nextBtn = true
        } else {
          this.nextBtn = false
        }
        // console.log(this.pid)
      },

      //右侧点击阅读
      detail: function(e) {
        var target = e.target || e.srcElement
        var pId = target.attributes["pid"].nodeValue
        console.log(pId)
        // this.$router.push({
        //   path: "/blog/main/" + pId
        // })
        // this.pid = pId
        // this.loading = true
        // this.getData()
      },

      //下一篇文章
      next() {
        // console.log(this.pid)
        this.$router.push({
          path: "/blog/main/" + this.pid
        })
        this.loading = true
        this.getData()
      },

      back() {
        this.$router.push({
          path: "/blog"
        })
        document.title = "渣狗啊喂"
      },

      //二维码动作函数
      pop() {
        this.popup = !this.popup
      }
    }
  }
</script>

<style scoped>
  .popup {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-color: rgba(255, 255, 255, 0.8);
  }

  .popup img {
    top: 50%;
    left: 50%;
    width: 25%;
    position: absolute;
    border-radius: 25px;
    transform: translate(-50%, -50%);
  }

  .reward {
    float: right;
    cursor: pointer;
  }

  .go-back {
    cursor: pointer;
  }

  .detail {
    margin-top: 30px;
    padding-bottom: 10px;
    white-space: pre-wrap;
    border-bottom: 1px solid #eee;
  }

  .main-top {
    color: #555;
    font-size: 24px;
    padding-top: 23px;
    padding-bottom: 5px;
    text-align: center;
  }

  .ptitle {
    font-weight: bold;
  }

  .pview {
    color: #999;
    font-size: 12px;
    padding-top: 7px;
    line-height: 17px;
    text-align: right;
  }

  .article {
    font-size: 16px;
  }

  .ptime {
    color: #777;
    float: left;
    cursor: pointer;
    font-size: 13px;
    padding: 15px 0 10px 0;
  }

  .next {
    float: right;
  }

  .passages-other {
    padding-top: 15px;
    padding-bottom: 20px;
  }

  .passages-other div {
    height: 35px;
    line-height: 35px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .other-keywords {
    color: #999;
    font-size: 13px;
    padding-left: 15px;
  }

  .passages-other span {
    cursor: pointer;
  }

  .passage-hot {
    margin-bottom: 40px;
  }

  @media screen and (max-width: 768px) {
    .passageContent {
      padding-left: 15px;
      padding-right: 15px;
    }

    .popup img {
      width: 80%;
    }

    .pview {
      text-align: center;
    }
  }
</style>



