import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pNumber: 0,
    pid_array: []
  },
  mutations: {
    dataChange(state, n) {
      state.pNumber = n
    },
    pid_array_change(state, arr) {
      state.pid_array = arr
    }
  }
})



